body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  font: 1em sans-serif;
}
.Wrapper {
  width: 1024px; 
  margin: 0 auto;
  text-align: center;
}

@media (max-width: 1024px) {
  .Wrapper {
    width: 90%;
  }
}
.Table__img {
    max-height: 2em;
    margin: auto;
    display: block;
    border-radius: 3px;
  }
  
  table {
    border-collapse: collapse;
    margin: 0 auto;
  }
  
  td,
  th {
    border: thin solid #aaa;
    text-align: center;
    background-color: aliceblue;
    padding: 0.5em;
  }
  
  th {
    background-color: #40a0c3;
    color: white;
  }
  .fa {
    margin: 5px;
  }
 .fa-trash {
   color: rgb(134, 44, 44);
 }
 .fa-pencil {
   color: rgb(21, 71, 21);
 }
.ReadItem {
    width: 500px;
    margin: 10% auto;
    border: 1px solid black;
    border-radius: 10px;
    padding: 10px;
    position: absolute;
    background: #40a0c3;
    left: 0; right: 0;
}
.overlay {
    position: fixed; 
    left: 0; right: 0; top: 0; bottom: 0; 
    background: rgba(0, 0, 0, 0.8); 
    height: 100%;
   }
input {
    width: 100%;
    height: 25px;
}
.AddItem {
    width: 500px;
    margin: 10% auto;
    border: 1px solid black;
    border-radius: 10px;
    padding: 10px;
    position: absolute;
    background: #40a0c3;
    left: 0; right: 0;
}
.overlay {
    position: fixed; 
    left: 0; right: 0; top: 0; bottom: 0; 
    background: rgba(0, 0, 0, 0.8); 
   }
input {
    width: 100%;
    height: 25px;
}
button {
    border-radius: 5px;
    background: #aed2ae;
    padding: 10px;
    font-size: 15px;
    font-weight: bold;
    margin: 10px 0;
}
button:hover {
    cursor: pointer;
}
.ItemAuto {
    width: 250px;
    border-radius: 8px;
    text-align: center;
    border: thin solid #aaa;
    background-color: aliceblue;
    color: #40a0c3;
    margin: 5px;
}
.ItemAuto__img{
    width: 100%;
    height: 150px;
}
.ItemAuto__name {
    border-bottom: 1px dotted black;
    font-weight: bold;
    background: #aed2ae;
}
.ListItem {
    display: flex;
    flex-wrap: wrap;
    margin-top: 20px;
    justify-content: space-around;
}
